.m-list-optionlist {
  position: absolute;
  box-sizing: border-box;
  display: none;
  min-width: 300px;
  margin-top: 0.5em;
  border: 1px solid #455B97;
  border-radius: 10px;
  padding: 0.5em;
  background-color: white;
  z-index: 999;
  max-height: 200px;
  overflow: scroll;
}

.focused ~ .m-list-optionlist {
  display: inline-block;
}